@font-face {
    font-family: 'Muli-Regular'; /*You can use whatever name that you want*/
    src: url('./Muli-Regular.ttf');
}

body {
  margin: 0;
  font-family: Muli-Regular, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: Muli-Regular, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
